html {
  --active-color: #00acdc;
}

body {
  margin: 0;
}

.header {
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.header-logo {
  width: 40px;
  height: 40px;
}

.header-logo img {
  max-width: 100%;
}

.nav {
  align-items: center;
  height: 100%;
  display: flex;
}

.nav-list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-item {
  text-align: center;
  align-items: center;
  display: flex;
  position: relative;
}

.nav-item:not(:last-child) {
  margin-right: 20px;
}

.nav-item .nav-link {
  text-align: center;
  color: #000;
  align-items: center;
  height: 60px;
  display: flex;
  position: relative;
}

.nav-item .is-active {
  color: var(--active-color);
}

.is-active:after, .nav-item .nav-link:active:after {
  content: "";
  background-color: var(--active-color);
  border-radius: 10px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.nav-item .nav-link:link {
  text-decoration: none;
}

.nav-item .nav-link:hover {
  color: var(--active-color);
}

